/* src/Components/WorkDataModal.css */

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: #fff;
    padding: 1.5rem 2rem;
    border-radius: 8px;
    max-width: 900px;
    width: 90%;
    max-height: 90%;
    overflow-y: auto;
  }
  
  .data-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 1rem;
    font-size: 0.95rem;
  }
  
  .data-table th,
  .data-table td {
    border: 1px solid #ccc;
    padding: 0.5rem;
    text-align: center;
  }
  
  .warning-message {
    color: red;
    margin-top: 0.75rem;
    font-size: 0.95rem;
  }
  
  .enter-data-button {
    padding: 0.75rem 1.5rem;
    background-color: #3182ce;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  .save-button {
    padding: 0.3rem 0.75rem;
    background-color: #38a169;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .edit-button {
    padding: 0.3rem 0.75rem;
    background-color: #ed8936;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .delete-button {
    padding: 0.3rem 0.75rem;
    background-color: #e53e3e;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
  }
  
  .close-button {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #3182ce;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.95rem;
  }
  
  .subRow {
    background-color: #f9f9f9;
    padding: 0.75rem 1rem;
    border-radius: 4px;
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
  }
  